	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"ЧЕРНОКНИЖНИЦА","type":"lyrics"},"html":"<h1>ЧЕРНОКНИЖНИЦА</h1>\n<section>\n<p>Ничего не пишется — <br>\nстал я выше месяца.<br>\nЭто чернокнижница,<br>\nсерп сковав из месяца,<br>\nсрезала мне волосы — <br>\nсделала обманутым.<br>\nЗагремел я совестью,<br>\nспрятанной карманами.<br>\nЯ на стол их вывернул — <br>\nтам игра да золото<br>\nставят перед выбором,<br>\nзагибают воротом.<br>\nВесь я перемешанный<br>\nс памятью о будущем<br>\nпревращаюсь в пешего<br>\nв человечье чудище.</p>\n<p>Ой да ой!<br>\nЭль да лай!<br>\nПой да пой,<br>\nда играй!<br>\nДоиграй до запетых<br>\nдо оборванных нот — <br>\nи ты срежешь секрет<br>\nэтих рук, будто плод<br>\nОй да ой!<br>\nЛай да лай!<br>\nПой да пой,<br>\nда играй.<br>\nДоиграй до конца,<br>\nи тогда я войду.<br>\nУ начала кольца<br>\nрыба бьётся во льду.</p>\n<p>Ой ты, чернокнижница,<br>\nгде черешня лопнула,<br>\nтам ничто не движется — <br>\nпустота да стёкла там.<br>\nНи во что не верится.<br>\nНичего не кружится.<br>\nБелый месяц пенится,<br>\nзвёзды будто кружева.<br>\nНочь моргает с берега.<br>\nСлышишь, чернокнижница!<br>\nТы меня не сберегла<br>\nСлышишь, слышишь — пишется!<br>\nСлышишь, расступается!<br>\nПесня распоясалась!<br>\nРыба бултыхается,<br>\nтолько бают ясли нас.</p>\n<p>Эль да лэй!<br>\nЛай да лал!<br>\nПел да пел,<br>\nда играл.<br>\nДоиграл до запетых<br>\nдо оборванных нот — <br>\nи ты срезал секрет<br>\nэтих рук, будто плод.<br>\nОй да ой!<br>\nЛай да лай!<br>\nПой да пой,<br>\nда играй!<br>\nДоиграй до конца,<br>\nи тогда мы войдём — <br>\nу начала кольца<br>\nбудем рыбой вдвоём.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/ru/lyrics/chernoknizhnica/"}}

